import { memo, useState } from "react"
import "./style.css"
import { BrandName, Hamburger, Navigation } from "./style";
import { HamburgerIcon } from "icons";

const Navbar = memo(() =>{
  const [isNavExpanded, setIsNavExpanded] = useState(false)

  return (
    <Navigation>
      <BrandName href="/react-task-management/home">
        Task Manager
      </BrandName>

      <Hamburger onClick={() => {
          setIsNavExpanded(!isNavExpanded)
        }}>
        <HamburgerIcon />
      </Hamburger>
      <div
        className={
          isNavExpanded ? "navigation-menu expanded" : "navigation-menu"
        }
      >
        <ul>
          <li>
            <a href="/react-task-management/home">Home</a>
          </li>
          <li>
            <a href="/react-task-management/categories">Categories</a>
          </li>
          <li>
            <a href="/react-task-management/tasks">Tasks</a>
          </li>
        </ul>
      </div>
    </Navigation>
  );
})

export default Navbar